		  >
		  <el-header class="header">
		          <!-- 搜索表单 start -->
		          <el-form
		            :inline="true"
		            :model="searchValidateFormRole"
		            ref="searchValidateFormRole"
		            label-width="100px"
		            class="search-validate-form"
		            :rules="searchValidateFormRoleRules"
		          >
		  		  
		  		  <!-- 请选择商店 start -->
		  		  <el-form-item label="商店" prop="shopId">
		  		  			<el-select
		  		  				v-model="searchValidateFormRole.shopId"
		  		  				placeholder="请选择商店"
		  		  				filterable
		  		  				clearable
