		  		  			>
		  		  				<el-option
		  		  					v-for="item in shopList"
		  		  					:key="item.shopId"
		  		  					:label="item.shopName"
		  		  					:value="item.shopId"
		  		  				/>
		  		  			</el-select>
		  		  				
		  		  </el-form-item>
		  		   <!-- 请选择商店 end -->
		  		  
		  		  
		  
		          </el-form>
		          <!-- 搜索表单 end -->
		  
		          <!-- 查询数据 start -->
		          <el-button
		            type="primary"
		            icon="el-icon-search"
		            @click="onSearchRole('searchValidateFormRole')"
		            >搜索</el-button
		          >
		          <!-- 查询数据 end -->
		  
		          <!-- 重置 start -->
		          <el-button type="primary" icon="el-icon-refresh-right" @click="onResetRole"
		            >重置</el-button
		          >
		          <!-- 重置 end -->
		  		
		  		<el-button
		  		     type="primary"
		  		     icon="el-icon-circle-plus-outline"
		  		     @click="onAddRole"
		  		     >新增</el-button
		  		   >
		        </el-header>
		    <el-table
		      class="table"
		      :data="tableDataRole"
		      style="width: 100%"
		      border
		      v-loading="tableLoading"
		      :header-cell-style="{
		        background: '#f2f2f2',
		        color: '#606266',
		        height: '38px',
		        padding: '0px'
		      }"
