		    >
		      <el-table-column
		        v-for="(value, key) in tableFieldsRole"
		        :key="key"
		        :prop="value"
		        :label="key"
		        min-width="130"
		      >
		        <template slot-scope="scope">
		          <div>
		            {{ tableContentValueRole(scope.row, value) }}
		          </div>
		        </template>
		      </el-table-column>
		      <el-table-column label="操作" min-width="80">
		        <template slot-scope="scope">
		  			<el-button
		  			    size="small"
		  			    type="danger"
		  			    @click.native.prevent="deleteRowRole(scope.$index, scope.row)"
		  			    >删除</el-button
		  			>
		        </template>
		      </el-table-column>
		    </el-table>
		    <!-- 表格 end -->
		  
		    <!-- 导出的表格 start -->
		    <!-- 此表格始终保持隐藏 -->
		    <el-table
		      id="outTable"
		      class="table"
		      :data="exportTableData"
		      style="width: 100%; display: none"
		      border
		    >
		      <el-table-column
		        v-for="(value, key) in tableFields"
		        :key="key"
		        :prop="value"
		        :label="key"
		        min-width="130"
		        align="center"
		      >
		      </el-table-column>
		    </el-table>
		    <!-- 导出的表格 end -->
		  </el-main>
		  
		  <el-footer>
		    <!-- 分页 start -->
		    <div class="block">
		      <el-pagination
		        background
		        layout="prev, pager, next, total, sizes, jumper"
		        :total="total"
		        :current-page="currentPage"
		        :page-sizes="[10, 20, 30, 40]"
		        :page-size="pageSize"
		        @current-change="onCurrentChange"
		        @size-change="onSizeChange"
		      >
		      </el-pagination>
		    </div>
		    <!-- 分页 end -->
		  </el-footer>
		     
		</el-dialog>
		<!-- 权限详情 -->
		
		<!-- 添加或编辑权限详情 -->
		<el-dialog
			    class="dialog"
				width="25%"
			    :title="drawerTitleRole + '权限详情'"
			    :visible.sync="drawerRoleAdd"
			    :before-close="handleCloseRoleAdd"
			    :close-on-click-modal="false"
			  >
			      <el-form
			        ref="validateFormRole"
			        :model="validateFormRole"
			        label-width="100px"
			        class="demo-ruleForm"
			        :size="formSize"
			        status-icon
					style="width:100%"
					:rules="validateFormRoleRules"
			      >
					  <el-form-item label="商店名称" prop="shopId">
					  	<el-select
					  		v-model="validateFormRole.shopId"
					  		placeholder="请选择商店"
					  		filterable
					  		clearable
					  		style="width: 100%"
					  	>
					  		<el-option
					  			v-for="item in shopList"
					  			:key="item.shopId"
					  			:label="item.shopName"
					  			:value="item.shopId"
					  		/>
					  	</el-select>
					  </el-form-item>
			      </el-form>
				  <div class="submit">
				    <el-button type="primary" @click="submitFormRole('validateFormRole')"
				      >提交</el-button
				    >
				    <el-button @click="cancelClickRole('validateFormRole')">取消</el-button>
				  </div>
			</el-dialog>
			<!-- 添加或编辑权限详情 -->
    </el-container>
