</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { createUrl,formatDate } from '../utils/utils.js'
export default {
  name: 'SettlementFlow',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      userRoleList: [
        {
          value: '0',
          label: '暂无角色'
        },
        {
          value: '1',
          label: '商家'
        },
		{
		  value: '2',
		  label: '店员'
		}
      ], // 入库类型选项

      dateStateOptions: [
        {
          value: '1',
          label: '按月查询'
        },
        {
          value: '2',
          label: '按日查询'
        },
        {
          value: '3',
          label: '按年查询'
        }
      ], // 日期统计方式选项



      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画
      importLoading: false, // 导入excel按钮的加载动画

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      dateType: 'daterange', // 日期类型（月，日，年）
      valueFormat: 'yyyy-MM-dd', // 日期格式

      searchValidateForm: {}, // 搜索表单验证对象
	  searchValidateFormRole:{},



      downloadUrl: '',

      total: 0,



      // 搜索表单规则
      searchValidateFormRules: {},
	  searchValidateFormRoleRules:{},

      // 提交表单规则
      validateFormRules: {

		userPhone:[
			{ required: true, message: '请输入手机号', trigger: 'blur' }
		],
		userRole:[
			{ required: true, message: '请选择角色', trigger: 'change' }
		],
      },
	  validateFormRoleRules:{
		  shopId:[
		  	{ required: true, message: '请选择商店', trigger: 'change' }
		  ],
	  },

	  //新增
	  showTable: true, // 是否显示表格
	    tableFields: {
	  		// 商店名称:'shopName',
			// 用户名称:'shopUser',
	  		用户手机号:'userPhone',
			用户角色:'userRole',
			创建时间:'createDate',
			更新时间:'updateDate'
	    },
	    tableData: [], // 表格数据
		
		tableFieldsRole: {
			商店名称:'shopName',
			景区名称:'scenicSpotName',
			// 
			// 用户名称:'shopUser',
			// 用户手机号:'userPhone',
			// 用户角色:'userRole',
			创建时间:'createDate',
			更新时间:'updateDate'
		},
		tableDataRole: [], // 表格数据
	    tableLoading: false,
	  spotList:[],
	  shopList:[],
	  drawer:false,
	  direction:'rtl',
	  drawerTitle:'',
	  drawerTitleRole:'',
	  
	  drawerRole:false,
	  drawerRoleAdd:false,
	  formSize:'',
	  validateForm: {}, // 表单验证对象
	  
	  validateFormRole:{},
	  imgBaseUrl:'https://jxzy-robot.oss-cn-beijing.aliyuncs.com/'
    }
  },
  created() {
    // 如果当前环境是开发环境才调用登录方法
    // if (process.env.NODE_ENV == 'development') {
    //   this.login()
    // }

    this.getSearchValidateForm()
	this.getSearchValidateFormRole()
    this.getValidateForm()
	this.getSpotList()
	this.getShopList()
    this.initTableData()
  },
  beforeMount() {},
  mounted() {},
  methods: {
	  // 点击删除时触发
	  deleteRow(index, row) {
	    this.$confirm('此操作将删除该行数据, 是否继续?', '提示', {
	      confirmButtonText: '确定',
	      cancelButtonText: '取消',
	      type: 'warning'
	    })
	      .then(async () => {
			const params = new URLSearchParams() // 创建对象
			params.append('shopUserId', row.shopUserId) // 封装对象
			params.append('shopId', row.shopId) // 封装对象
	        const { data: res } = await this.$http.get(
	          '/Merchant/shopUser/deleteShopUser',
	          {
	            params
	          }
	        )
	        if (res.state == '200') {
	          this.$message({
	            type: 'success',
	            message: res.msg,
	            offset: 100,
	            center: true,
	            duration: 1000
	          })
	          this.initTableData()
	        } else {
	          this.$message({
	            type: 'error',
	            message: res.msg,
	            offset: 100,
	            center: true,
	            duration: 3000
	          })
	        }
	      })
	      .catch(() => {
	        this.$message({
	          type: 'info',
	          message: '已取消删除',
	          offset: 100,
	          center: true,
	          duration: 1000
	        })
	      })
	  },
	  
	  deleteRowRole(index, row){
		  console.log(row)
		  this.$confirm('此操作将删除该行数据, 是否继续?', '提示', {
		    confirmButtonText: '确定',
		    cancelButtonText: '取消',
		    type: 'warning'
		  })
		    .then(async () => {
		  	const params = new URLSearchParams() // 创建对象
		  	params.append('bindingId', row.bindingId) // 封装对象
		      const { data: res } = await this.$http.get(
		        '/Merchant/shopUser/delectUserPermission',
		        {
		          params
		        }
		      )
		      if (res.state == '200') {
		        this.$message({
		          type: 'success',
		          message: res.msg,
		          offset: 100,
		          center: true,
		          duration: 1000
		        })
		        this.initTableDataRole()
		      } else {
		        this.$message({
		          type: 'error',
		          message: res.msg,
		          offset: 100,
		          center: true,
		          duration: 3000
		        })
		      }
		    })
		    .catch(() => {
		      this.$message({
		        type: 'info',
		        message: '已取消删除',
		        offset: 100,
		        center: true,
		        duration: 1000
		      })
		    })
	  },
	  cancelClick(formName){
		  this.drawer = false
		   this.$refs[formName].resetFields();
		  // this.validateForm = {}
	  },
	  cancelClickRole(formName){
		  this.drawerRoleAdd = false
		   this.$refs[formName].resetFields();
	  },
	  async getSpotList(){
	  		  const { data: res } = await this.$http.get('/Merchant/sysShop/getScenicList')
	  		  if(res.state == 200){
	  			  this.spotList = res.data
	  		  }
	  		  console.log(res)
	  },
	  async getShopList(){
	  	const { data: res } = await this.$http.post('/Merchant/sysShop/getShopList')
	  	if(res.state == 200){
	  		this.shopList = res.data.list
	  	}
	  	console.log(res)
	  },
    // 获取搜索表单验证对象
    getSearchValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.searchValidateForm = {
        ...validateForm,
      }
    },
	getSearchValidateFormRole(){
		var validateFormRole = {}
		for (var item in this.tableFieldsRole) {
		  validateFormRole[this.tableFieldsRole[item]] = ''
		}
		this.searchValidateFormRole = {
		  ...validateFormRole,
		}
	},

    // 获取提交表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = validateForm
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=houjingchen&password=houjingchen'
      )
    },

    /**
     * 初始化表格数据
     * @params - isPage - 是否分页
     */
    async initTableData(isPage = false) {
      this.tableLoading = true
	  const params = new URLSearchParams() // 创建对象
		params.append('shopId', this.searchValidateForm.shopId || '') // 封装对象
		params.append('userPhone', this.searchValidateForm.userPhone) // 封装对象
	      params.append('pageSize', this.currentPage) // 封装对象
	      params.append('pageNum', this.pageSize) // 封装对象
	const { data: res } = await this.$http.post(
        '/Merchant/shopUser/getShopUser',params)
      if (res.state == '200') {
		this.tableData = res.data.list
		this.total = res.data.total
        this.tableLoading = false
      }
    },
	
	async initTableDataRole(){
		this.tableLoading = true
		  const params = new URLSearchParams() // 创建对象
			  params.append('shopUserId', this.searchValidateFormRole.shopUserId || '') // 封装对象
			  params.append('shopId', this.searchValidateFormRole.shopId || '') // 封装对象
		const { data: res } = await this.$http.post(
		    '/Merchant/shopUser/getShopUserPermission',params)
		  if (res.state == '200') {
			this.tableDataRole = res.data
			// this.total = res.data.total
		    this.tableLoading = false
		  }
	},

    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData() {
      var startDate =
        (this.searchStartEndDate && this.searchStartEndDate[0]) || null
      var endDate =
        (this.searchStartEndDate && this.searchStartEndDate[1]) || null

      // console.log(startDate, endDate)

      const { data: res } = await this.$http.get(
        '/system/inventory_detail/downloadExcel',
        {
          params: {
            ...this.searchValidateForm,
            startDate,
            endDate,
            type: 1 // 1、入库；2、出库
          },
          responseType: 'arraybuffer' // 这里必须制定响应的数据类型为arraybuffer，否则导出的文件无法打开
        }
      )
      if (res) {
        const url = window.URL.createObjectURL(
          new Blob([res], {
            type:
              'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
          })
        )
        const a = document.createElement('a')
        console.log(url)
        a.href = url
        a.download = '入库信息'
        a.click()

        this.$message({
          message: '导出成功',
          type: 'success'
        })
      } else {
        this.$message({
          message: '导入失败',
          type: 'error'
        })
      }
    },

    // 导入EXCEL
    async importE(file) {
      this.importLoading = true
      var formData = new FormData()
      formData.append('file', file)

      const { data: res } = await this.$http.post(
        '/system/inventory_detail/importExcel',
        formData
      )
      this.importLoading = false
      if (res.state == '200') {
        this.$message({
          message: '导入成功！',
          type: 'success'
        })
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error'
        })
      }
    },




    // 重置
    onReset() {
      // 清空所有输入框内容
      this.getSearchValidateForm()
      this.searchStartEndDate = []
      this.selectDateState = '1'
      this.dateType = 'monthrange'

      // 重新获取表格数据
      this.initTableData()
    },
	
	onResetRole(){
		this.searchValidateFormRole.shopId = ''
		// this.getSearchValidateFormRole()
		this.initTableDataRole()
	},

    // 点击查询数据按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.showTable = true
          // console.log(this.searchValidateForm);
          this.initTableData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
	
	onSearchRole(formName){
		this.$refs[formName].validate((valid) => {
		  if (valid) {
		    this.initTableDataRole()
		  } else {
		    console.log('error submit!!')
		    return false
		  }
		})
	},


    // 新增
    onAdd() {
		this.drawer = true
		this.drawerTitle = '添加'
		this.validateForm = {}
    },
	
	onAddRole(){
		this.drawerRoleAdd = true
		this.drawerTitleRole = '添加'
	},

    // 点击编辑时触发
    editRow(index, row) {
		console.log(row)
      this.drawer = true
      this.drawerTitle = '编辑'
	  var rowObj = Object.assign({}, row) //
		this.validateForm = rowObj
    },
	
	//角色详情
	roleRow(index,row){
		console.log(row)
		console.log(index)
		this.drawerRole = true
		var rowObj = Object.assign({}, row) //
		this.validateFormRole.userRole = rowObj.userRole
		this.validateFormRole.shopUserId = rowObj.shopUserId
		this.searchValidateFormRole.shopUserId = rowObj.shopUserId
		this.initTableDataRole()
		// submitFormRole
	},
    // dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      // this.ValidateForm = {}
      // 调用子组件里的 resetFields 方法
      // this.$refs.stockList.resetFields()
      this.$refs.validateForm.resetFields(); // 重置表单（清空无效）
      // this.getValidateForm() // 将validateForm对象中各个属性清空
    },
	
	handleCloseRole(done){
		done() 
	},
	handleCloseRoleAdd(done){
		done() 
	},

    // dialog提交
    submitForm(formName) {
		this.$refs[formName].validate((valid) => {
		    if (valid) {
		        if (this.drawerTitle == '添加') {
		          // 新增工厂发货信息
		          this.addTableData()
		          this.drawer = false
		        } else if (this.drawerTitle == '编辑') {
		          // 编辑工厂发货信息
		          this.editTableData()
		          this.drawer = false
		        } 
		        this.$refs.validateForm.resetFields();
		    } else {
				console.log('error submit!!');
				return false;
		    }
		});
		
    },
	submitFormRole(formName){
		this.$refs[formName].validate((valid) => {
		    if (valid) {
		        if (this.drawerTitleRole == '添加') {
		          // 新增工厂发货信息
		          this.addTableDataRole()
		          this.drawer = false
		        } else if (this.drawerTitleRole == '编辑') {
		          // 编辑工厂发货信息
		          this.editTableDataRole()
		          this.drawer = false
		        } 
		        this.$refs.validateFormRole.resetFields();
		    } else {
				console.log('error submit!!');
				return false;
		    }
		});
	},
	
	async addTableData(){
		console.log(this.validateForm)
		const params = new URLSearchParams() // 创建对象
			// params.append('shopId', this.validateForm.shopId) // 封装对象
			// params.append('shopUserName', this.validateForm.shopUserName) // 封装对象
			params.append('openId', 'openId') // 封装对象
		    params.append('userPhone', this.validateForm.userPhone) // 封装对象
		    params.append('userRole', this.validateForm.userRole) // 封装对象
		const { data: res } = await this.$http.post(
		  '/Merchant/shopUser/addShopUser',
		  params
		)
		if (res.state == '200') {
		  this.$message({
		    message: res.msg,
		    type: 'success',
		    offset: 100,
		    center: true,
		    duration: 1000
		  })
		  this.initTableData()
		} else {
		  this.$message({
		    message: res.msg,
		    type: 'error',
		    offset: 100,
		    center: true,
		    duration: 3000
		  })
		}
	},
	
	async addTableDataRole(){
		// console.log(this.validateForm)
		const params = new URLSearchParams() // 创建对象
			params.append('shopId', this.validateFormRole.shopId) // 封装对象
		    params.append('userRole', this.validateFormRole.userRole) // 封装对象
			params.append('shopUserId', this.validateFormRole.shopUserId) // 封装对象
		const { data: res } = await this.$http.post(
		  '/Merchant/shopUser/addUserPermission',
		  params
		)
		if (res.state == '200') {
		  this.$message({
		    message: res.msg,
		    type: 'success',
		    offset: 100,
		    center: true,
		    duration: 1000
		  })
		  this.initTableDataRole()
		  this.drawerRoleAdd = false
		} else {
		  this.$message({
		    message: res.msg,
		    type: 'error',
		    offset: 100,
		    center: true,
		    duration: 3000
		  })
		}
	},
	
	async editTableData(){
		const params = new URLSearchParams() // 创建对象
			// params.append('shopId', this.validateForm.shopId) // 封装对象
			// params.append('shopUserName', this.validateForm.shopUserName) // 封装对象
			params.append('userPhone', this.validateForm.userPhone) // 封装对象
			params.append('userRole', this.validateForm.userRole) // 封装对象
			params.append('shopUserId', this.validateForm.shopUserId) // 封装对象
		const { data: res } = await this.$http.post(
		  '/Merchant/shopUser/updateShopUser',
		  params
		)
		if (res.state == '200') {
		  this.$message({
		    message: res.msg,
		    type: 'success',
		    offset: 100,
		    center: true,
		    duration: 1000
		  })
		  this.initTableData()
		} else {
		  this.$message({
		    message: res.msg,
		    type: 'error',
		    offset: 100,
		    center: true,
		    duration: 3000
		  })
		}
	},

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData(true)
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData(true)
    },

    // 文件状态改变时的钩子，添加文件、上传成功和上传失败时都会被调用
    handleChange(file, fileList) {
      console.log(file)
      this.fileTemp = file.raw

      // 导入判断
      if (this.fileTemp) {
        if (
          this.fileTemp.type ==
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' ||
          this.fileTemp.type == 'application/vnd.ms-excel'
        ) {
          this.importfxx(this.fileTemp)
        } else {
          this.$message({
            type: 'warning',
            message: '附件格式错误，请删除后重新上传！'
          })
        }
      } else {
        this.$message({
          type: 'warning',
          message: '请上传附件！'
        })
      }
    },

    // 文件超出个数限制时的钩子
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      )
    },

    // 文件列表移除文件时的钩子
    handleRemove(file, fileList) {
      this.fileTemp = null
    },

    // 导入格式
    importfxx(obj) {
      let _this = this
      // 通过DOM取文件数据
      this.file = obj
      var rABS = false //是否将文件读取为二进制字符串
      var f = this.file
      var reader = new FileReader()
      //if (!FileReader.prototype.readAsBinaryString) {
      FileReader.prototype.readAsBinaryString = function(f) {
        var binary = ''
        var rABS = false //是否将文件读取为二进制字符串
        var pt = this
        var wb //读取完成的数据
        var outdata
        var reader = new FileReader()
        reader.onload = function(e) {
          var bytes = new Uint8Array(reader.result)
          var length = bytes.byteLength
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i])
          }
          var XLSX = require('xlsx')
          if (rABS) {
            wb = XLSX.read(btoa(fixdata(binary)), {
              //手动转化
              type: 'base64'
            })
          } else {
            wb = XLSX.read(binary, {
              type: 'binary'
            })
          }
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]) //outdata就是你想要的东西
          console.log(outdata)
          this.da = [...outdata]
          let arr = []
          this.da.map((v) => {
            let obj = {
              robotCount: v['机器人数量'],
              type: v['发货进度'],
              applicantName: v['申请人'],
              factoryName: v['发货工厂'],
              consignorName: v['发货人'],
              receivingName: v['收货景区'],
              consigneeName: v['收货人'],
              consigneePhone: v['收货人电话'],
              receiningAddress: v['收货地址'],
              consignmentDate: v['发货时间']
            }
            arr.push(obj)
          })
          // 将 arr 赋值给 tableData，来更新表格数据
          this.tableData = arr
          return arr
        }
        reader.readAsArrayBuffer(f)
      }

      if (rABS) {
        reader.readAsArrayBuffer(f)
      } else {
        reader.readAsBinaryString(f)
      }
    },

    // 导入EXCEL文件超出限制时触发
    handleImportExceed(files) {
      this.$refs.uploadImport.clearFiles()
      // const file = files[0]
      this.$refs.uploadImport.submit(files)
    },

    // 导入EXCEL文件状态改变时触发
    handleImportChange(uploadFile, uploadFiles) {
      console.log(uploadFile)
      this.importE(uploadFile.raw)
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
    // StockList
  },
  computed: {
	  setTreasuretype(){
		  return (state) =>{
			  var result = ''
			  if (state == '1') {
			    result = '代金卷'
			  } else if (state == '2') {
			    result = '免单'
			  } else if(state == '3'){
			    result = '实物'
			  }else{
			  	result = '谢谢参与'
			  }
			  return result
		  }
	  },
	  setPrizeSize(){
		  return (state) =>{
		  			  var result = ''
		  			 if (state == '0') {
		  			   result = '普通奖品'
		  			 } else if (state == '1') {
		  			   result = '大奖'
		  			 } else {
		  			   result = '暂无'
		  			 }
		  			  return result
		  }
	  },
	  setProbability(){
		  return (state) =>{
				var result = state.split("-")
				console.log(result)
				if(result[1] == 9){
					return parseFloat(state).toFixed(9) + '%'
				}else if(result[1] == 8){
					return parseFloat(state).toFixed(8) + '%'
				}else if(result[1] == 7){
					return parseFloat(state).toFixed(7) + '%'
				}else if(result[1] == 6){
					return parseFloat(state).toFixed(6) + '%'
				}else if(result[1] == 5){
					return parseFloat(state).toFixed(5) + '%'
				}else if(result[1] == 4){
					return parseFloat(state).toFixed(4) + '%'
				}else {
					var arr = parseFloat(state).toFixed(10)
					return parseFloat(arr) + '%'
				}
		  }
	  },
    tableContentValue() {
      return (row, value) => {
        // console.log(row, value);
        var result = ''
        if (value == 'type') {
          if (row[value] == 1) {
            result = '待发货'
          } else if (row[value] == 2) {
            result = '已发货'
          } else if (row[value] == 3) {
            result = '已收货'
          } else if (row[value] == 4) {
            result = '已关闭'
          }
        } else if (value == 'inStockReason') {
          if (row[value] == 1) {
            result = '生产'
          } else if (row[value] == 2) {
            result = '销售'
          }
        } else if (value == 'inStockType') {
          if (row[value] == 1) {
            result = '采购'
          } else if (row[value] == 2) {
            result = '回收'
          }
        } else if(value == 'jackpotType'){
			if(row[value] == 1){
				 result = '定点寻宝'
			}else if(row[value] == 2){
				 result = '定时寻宝'
			}
		} else if(value == 'userRole'){
			if(row[value] == 1){
				 result = '商家'
			}else if(row[value] == 2){
				 result = '店员'
			}else{
				 result = '暂无角色'
			}
		}
		// 
		else {
          result = row[value] || '无'
        }
        return result
      }
    },
	tableContentValueRole(){
		return (row, value) => {
		  // console.log(row, value);
		  var result = ''
		  if (value == 'type') {
			  if (row[value] == 1) {
				result = '生产'
			  } else if (row[value] == 2) {
				result = '销售'
			  }
		  }else{
			  result = row[value] || '无'
		  }
		  return result
		}
	},
  }
}
</script>

